import Vue from 'vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import Axios from 'axios'
import VueHead from 'vue-head'

Vue.use(Vuex)
Vue.use(VueRouter)
Vue.use(VueHead)

import NavModel from './vuejs/components/NavModel.vue'
Vue.component('nav-model', NavModel)

import NavMechanism from './vuejs/components/NavMechanism.vue'
Vue.component('nav-mechanism', NavMechanism)

import NavTask from './vuejs/components/NavTask.vue'
Vue.component('nav-task', NavTask)

import ContentModal from './vuejs/components/ContentModal.vue'
Vue.component('content-modal', ContentModal)

import ContentDetail from './vuejs/components/ContentDetail.vue'
Vue.component('content-detail', ContentDetail)

import BookmarkModal from './vuejs/components/BookmarkModal.vue'
Vue.component('bookmark-modal', BookmarkModal)

import MechanismImage from './vuejs/components/MechanismImage.vue'
Vue.component('mechanism-image', MechanismImage)

import ApplicationTour from './vuejs/components/ApplicationTour'
Vue.component('application-tour',ApplicationTour)

import Storage from './vuejs/storage'
import mixins from './vuejs/mixins'
//import routing from './vuejs/routing'

let routing = {
    mode: 'hash',
    //base: '/',
    routes: [
        // dynamische Segmente beginnen mit Doppelpunkt
        { path: '/', name: 'home' },
        { path: '/instance/:instance', component: ContentModal, name: "instance"},
        { path: '/bookmarks', name: 'bookmarks', children: [
                {
                    path: 'entries/:entries',
                    name: 'bookmarkEntries'
                }
            ]
        }
    ]
}

Axios.get(host.uri + host.api)
    .then(function (response) {
        Storage.state.data = response['data']
        global.store = new Vuex.Store(Storage)
        global.router = new VueRouter(routing)

        new Vue({
            el: '#rkwDigiKit',
            store,
            router,
            mixins: [mixins],
            created: function () {
                //console.log(this.$route)
            },
            computed: {
                settings () {
                    return store.state.settings
                },
                digiKit () {
                    return store.state.data
                },
                activeItems () {
                    return store.state.runtime.activeItems
                },
                footer () {
                    return this.$store.state.data.footer
                },
                bookmarks () {
                    return store.state.runtime.bookmarks
                }
            },
            methods: {
                toggleBookmarkModal: function () {
                    this.$store.commit('toggleBookmarkModal')
                }
            },
            watch: {
                '$route' (to, from) {
                    if(this.$route.name !== "bookmarks") {
                        this.$store.commit('closeBookmarkModal')
                    }
                    if(this.$route.name !== "instance" && this.$route.name !== "bookmarks") {
                        this.$store.commit('unsetActiveItems')
                    }
                }
            }
        })
    })
    .catch(function (error) {
        console.log(error);
    });



