export default {
    methods: {
        findNextItem: function (siblings, needle) {
            let index = siblings.findIndex((el) => {
                return parseInt(el) === parseInt(needle)
            })
            if((index + 1) < siblings.length) return siblings[index + 1]
            if((index + 1) === siblings.length ) return siblings[0]
        },
        findPrevItem: function (siblings, needle) {
            let index = siblings.findIndex((el) => {
                return parseInt(el) === parseInt(needle)
            })
            if(index > 0) return siblings[index - 1]
            if(index === 0) return siblings[siblings.length - 1]
        },

        showTooltip: function(event) {
            let xOffset = 20,
                yOffset = 0,
                $link = event.target,
                $span = $link.nextElementSibling

            let linkPosition = $link.getBoundingClientRect();
            $span.style.top = event.pageY - linkPosition.top + yOffset + "px"
            $span.style.left = event.pageX - linkPosition.left + xOffset + "px"
            $span.style.display = "block"
        },
        hideTooltip: function(event) {
            let $link = event.target,
                $span = $link.nextElementSibling

            $span.style.display = "none"
        },
        moveMouse: function(event) {
            let xOffset = 20,
                yOffset = 20,
                $link = event.target,
                $span = $link.nextElementSibling

            $span.style.top = event.pageY  + yOffset + "px"
            $span.style.left = event.pageX  + xOffset + "px"
        },
        toggleBookmarkModal () {
            this.$store.commit('toggleBookmarkModal')
        },
        addBookmark (uid, target, event) {
            this.whooshToElement(event.target, document.getElementsByClassName(target)[0])
            this.$store.commit('addBookmark', uid)
            //console.log(this.$store.state.runtime.bookmarks.entries)
        },
        removeBookmark (uid) {
            this.$store.commit('removeBookmark', uid)
        },
        getContentModel (uid) {
            let task = this.$store.state.data['tasks'][this.$store.state.data['instances'][uid]['parent']]
            let mechanism = this.$store.state.data['mechanisms'][task['parent']]
            return this.$store.state.data['models']['uid' + mechanism['parent']]
            //return this.$store.state.settings.colors[model['color']]
        },
        getContentColor (uid) {
            return this.getContentModel(uid)['color']
        },
        isBookmarked (uid) {
            let el = this.$store.state.runtime.bookmarks.entries.find(bookmark => parseInt(bookmark) === parseInt(uid))
            return (typeof el !== 'undefined')
        },
        toggleTutorial () {
            this.$store.commit('toggleTutorial')
        },
        whooshToElement(startEl, targetEl) {
            let startCoords = startEl.getBoundingClientRect()
            let targetCoords = targetEl.getBoundingClientRect()

            let container = document.getElementById('rkwDigiKit')

            let clone = startEl.cloneNode(true)
            clone.addEventListener("transitionend", function(event) {
                event.currentTarget.remove()
                //container.removeChild(event.currentTarget)
            }, false)
            clone.style.top = startCoords.y + 'px'
            clone.style.left = startCoords.x + 'px'
            clone.classList.add('whooshElement')
            container.appendChild(clone)

            window.setTimeout(function () {
                clone.style.top = targetCoords.y + 'px'
                clone.style.left = targetCoords.x + 'px'
                clone.style.opacity = 0
            }, 100)

        }
    }
}


